import { nanoid } from 'nanoid'

const initialState = [
  [
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
  ],
  [
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
  ],
  [
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
  ],
  [
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
    null,
  ],
]
export const stateSlice = createSlice({
  name: 'dragCircuitState',
  initialState,
  reducers: {
    // 拖拽生成及修改门 根据门的不同情况进行修改二维数组
    dragGate: (state, { payload }) => {
      console.log(state,payload.coordinate.column,payload.geteType,'dragGate');
        let circuitGateOne = {}
        let circuitGateTow = {}
        let circuitGateThree = {}
        // console.log(payload.coordinate, 'payload.coordinate')
        // 释放拖拽过来的门
        if (payload.geteType) {
          switch (payload.geteType.bit) {
            // 当拖拽过来的门为1bit时
            case 1:
              circuitGateOne = {
                id: nanoid(),
                name: payload.geteType.name,
                row: payload.coordinate.row,
                column: payload.coordinate.column,
                isconnector: true,
                bit: 1,
                ranking: 0,
                line:null,
                col:null
              }
              // 判断当前位置是否有门
              // 当前位置没有门时
              if (state[payload.coordinate.column][payload.coordinate.row] == null) {
                state[payload.coordinate.column][payload.coordinate.row] = circuitGateOne
              } else {
                // 当前位置有门时
                switch (state[payload.coordinate.column][payload.coordinate.row].bit) {
                  // 当前位置门为1bit时
                  case 1:
                    state[payload.coordinate.column][payload.coordinate.row] = circuitGateOne
                    break
                  // 当前位置门为2bit时 再判断当前位置门处于第几个
                  case 2:
                    // 判断当前是2bit门的第几个
                    switch (
                      state[payload.coordinate.column][payload.coordinate.row].ranking
                    ) {
                      // 第一个时
                      case 0:
                        state[payload.coordinate.column][payload.coordinate.row] =
                          circuitGateOne
                        state[payload.coordinate.column + 1][payload.coordinate.row] = null
                        break
                      // 第二个时
                      case 1:
                        state[payload.coordinate.column][payload.coordinate.row] =
                          circuitGateOne
                        state[payload.coordinate.column - 1][payload.coordinate.row] = null
                        break
                      default:
                    }
                    break
                  // 放置的位置是3bit的门时 再判断当前位置门处于第几个
                  case 3:
                    switch (
                      state[payload.coordinate.column][payload.coordinate.row].ranking
                    ) {
                      // 第一个时
                      case 0:
                        state[payload.coordinate.column][payload.coordinate.row] =
                          circuitGateOne
                        state[payload.coordinate.column + 1][payload.coordinate.row] = null
                        state[payload.coordinate.column + 2][payload.coordinate.row] = null
                        break
                      // 第二个时
                      case 1:
                        state[payload.coordinate.column - 1][payload.coordinate.row] = null
    
                        state[payload.coordinate.column][payload.coordinate.row] =
                          circuitGateOne
                        state[payload.coordinate.column + 1][payload.coordinate.row] = null
    
                        break
                      case 2:
                        state[payload.coordinate.column - 2][payload.coordinate.row] = null
    
                        state[payload.coordinate.column - 1][payload.coordinate.row] = null
    
                        state[payload.coordinate.column][payload.coordinate.row] =
                          circuitGateOne
                        break
                      default:
                    }
                    break
                  default:
                }
              }
    
              break
            // 拖拽过来的门是2比特时
            case 2:
              let id = nanoid()
              circuitGateOne = {
                id,
                name: payload.geteType.name,
                row: payload.coordinate.row,
                column: payload.coordinate.column,
                isconnector: false,
                connector: 0,
                bit: 2,
                ranking: 0,
              }
    
              circuitGateTow = {
                id,
                name: payload.geteType.name,
                row: payload.coordinate.row,
                column: payload.coordinate.column,
                isconnector: true,
                connector: payload.coordinate.column - 1,
                bit: 2,
                ranking: 1,
              }
    
              switch (true) {
                case state[payload.coordinate.column][payload.coordinate.row]?.bit === 3 &&
                  state[payload.coordinate.column][payload.coordinate.row]?.ranking === 0:
                  state[payload.coordinate.column + 2][payload.coordinate.row] = null
    
                  break
                case state[payload.coordinate.column+1][payload.coordinate.row]?.bit === 3 &&
                  state[payload.coordinate.column+1][payload.coordinate.row]?.ranking === 0:
                  state[payload.coordinate.column + 2][payload.coordinate.row] = null
                  state[payload.coordinate.column + 3][payload.coordinate.row] = null
    
                  break
                case state[payload.coordinate.column + 1][payload.coordinate.row]?.bit ===
                  2 &&
                  state[payload.coordinate.column][payload.coordinate.row]?.ranking === 1:
                  state[payload.coordinate.column + 2][payload.coordinate.row] = null
                  state[payload.coordinate.column - 1][payload.coordinate.row] = null
                  break
                case state[payload.coordinate.column][payload.coordinate.row]?.ranking === 1:
                  state[payload.coordinate.column - 1][payload.coordinate.row] = null
                  break
                case state[payload.coordinate.column][payload.coordinate.row]?.ranking === 2:
                  state[payload.coordinate.column - 1][payload.coordinate.row] = null
                  state[payload.coordinate.column - 2][payload.coordinate.row] = null
                  break
                case state[payload.coordinate.column + 1][payload.coordinate.row]?.bit === 2:
                  state[payload.coordinate.column + 2][payload.coordinate.row] = null
                  break
    
                default:
              }
              state[payload.coordinate.column][payload.coordinate.row] = circuitGateOne
              state[payload.coordinate.column + 1][payload.coordinate.row] = circuitGateTow
              break
            case 3:
              let idThree = nanoid()
              circuitGateOne = {
                id: idThree,
                name: payload.geteType.name,
                row: payload.coordinate.row,
                column: payload.coordinate.column,
                isconnector: false,
                connector: 0,
                bit: 3,
                ranking: 0,
              }
    
              circuitGateTow = {
                id: idThree,
                name: payload.geteType.name,
                row: payload.coordinate.row,
                column: payload.coordinate.column,
                isconnector: false,
                connector: payload.coordinate.column - 1,
                bit: 3,
                ranking: 1,
              }
              circuitGateThree = {
                id: idThree,
                name: payload.geteType.name,
                row: payload.coordinate.row,
                column: payload.coordinate.column,
                isconnector: true,
                connector: payload.coordinate.column - 2,
                bit: 3,
                ranking: 2,
              }
              switch (true) {
                case state[payload.coordinate.column][payload.coordinate.row]?.ranking === 1:
                  state[payload.coordinate.column - 1][payload.coordinate.row] = null
                  break
                case state[payload.coordinate.column+1][payload.coordinate.row]?.ranking ===
                  0 && state[payload.coordinate.column+1][payload.coordinate.row]?.bit===3:
                  state[payload.coordinate.column +3][payload.coordinate.row] = null
                  break
                default:
              }
              state[payload.coordinate.column][payload.coordinate.row] = circuitGateOne
              state[payload.coordinate.column + 1][payload.coordinate.row] = circuitGateTow
              state[payload.coordinate.column + 2][payload.coordinate.row] = circuitGateThree
              break
            default:
          }
        }
    },
    //  删除门
    delGate: (state, { payload }) => {},
    // 新增qubit
    addQubit: (state, { payload }) => {},
  },
})
export const {dragGate,delGate,addQubit} = stateSlice.actions
export default stateSlice.reducer
